<template>
  <div>
    <div class="w">
      <!-- 景点轮播图 -->
      <el-carousel type="card" height="350px">
        <el-carousel-item v-for="(item, i) in imgList" :key="i">
          <img height="350" :src="root + item.ts_url" />
        </el-carousel-item>
      </el-carousel>
      <!-- 景点列表 -->
      <div class="scenic-list">
        <div class="list-item clearfix">
          <ul>
            <router-link :to="'/scenic/details/' + item.ti_id" v-for="item in sceniclist" :key="item.ti_id">
              <li>
                <img :src="item.ti_img.ttii_url || '../assets/img/1.jpg'" width="180" height="180" />
                <h3 class="title">
                  {{ item.ti_title || "商品名" }}
                </h3>
                <p class="desc">
                  {{ item.ti_context || "商品简介" }}
                </p>
              </li>
            </router-link>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      search: "",
      sceniclist: [],
      imgList: []
    }
  },
  created() {
    this.getsceniclist()
    this.getimglist()
  },
  methods: {
    async getsceniclist() {
      const { data: res } = await this.$http.get("info/showAll")
      if (res.code !== 200) return
      res.obj.some(item => {
        item.ti_img.ttii_url = this.root + item.ti_img.ttii_url
      })
      this.sceniclist = res.obj
      console.log(res)
    },
    async getimglist() {
      const { data: res } = await this.$http.get("scenery/showAll")
      console.log(res)
      if (res.code !== 200) return
      this.imgList = res.obj
    }
  },
  watch: {
    "$route.path": function(newval, oldval) {
      // if (newval.includes("scarch")) {
      //   this.search = this.$route.params.search
      //   this.getsceniclist()
      // }
    }
  }
}
</script>
<style lang="less" scoped>
.el-carousel {
  width: 1080px;
  margin: 0 auto;
}
.scenic-list {
  margin-top: 40px;
  .list-title {
    margin-bottom: 20px;
    span {
      margin-left: 30px;
      font-size: 24px;
    }
  }
  ul {
    padding: 0;
    list-style: none;
    li {
      float: left;
      padding: 20px 0;
      height: 380px;
      width: 286px;
      background-color: #fff;
      text-align: center;
      transition: all 0.5s;
      img {
        margin: 20px auto 25px;
      }
      .title {
        margin: 20px 10px 5px;
        font-size: 14px;
        font-weight: 400;
        color: #333;
      }
      .desc {
        margin: 10px 20px;
        height: 50px;
        font-size: 12px;
        color: #b0b0b0;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
      }
      .prices {
        color: #fa1d61;
      }
    }
    li:hover {
      transform: translateY(-10px);
      box-shadow: 1px 1px 15px 1px #00000030;
    }
    a:not(:nth-child(4n)) {
      > li {
        margin-right: 15px;
      }
    }
    a:not(:nth-child(-n + 4)) {
      > li {
        margin-top: 15px;
      }
    }
  }
}
</style>
