<template>
  <div class="login_container">
    <div />
    <div class="bg" />
    <div class="login_box">
      <h1>注册</h1>
      <!-- 注册表单 -->
      <el-form lable-width="0px" class="login-form" ref="registerFormref" :model="registerForm" :rules="rules">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input v-model="registerForm.username" ref="username" placeholder="用户名">
            <i slot="prefix" class="el-input__icon el-icon-user" />
          </el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input type="password" v-model="registerForm.password" ref="password" placeholder="密码">
            <i slot="prefix" class="el-input__icon el-icon-lock" />
          </el-input>
        </el-form-item>
        <!-- 确认密码 -->
        <el-form-item prop="repassword">
          <el-input type="password" v-model="registerForm.repassword" ref="password" placeholder="确认密码">
            <i slot="prefix" class="el-input__icon el-icon-lock" />
          </el-input>
        </el-form-item>
        <!-- 注册方式 -->
        <el-form-item label-width="80" label="注册方式">
          <el-select v-model="select" placeholder="请选择">
            <el-option label="用户" :value="1"> </el-option>
            <el-option label="商家" :value="2"> </el-option>
          </el-select>
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item>
          <el-button type="primary" @click="submitForm('registerFormref')">
            注册
          </el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="reback()" class="back">
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    const validaterepassword = (rele, value, callback) => {
      if (value === this.registerForm.password) return callback()
      callback(new Error("两次输入的密码不一致，请重新输入"))
    }
    const validatephone1 = async (rele, value, callback) => {
      const { data: res } = await this.$http.get("user/check", { params: { username: value } })
      if (res) {
        callback(new Error("用户已注册"))
      } else {
        callback()
      }
    }

    const validatePassWord = (rele, value, callback) => {
      const v_password = /^[a-zA-Z][a-zA-Z0-9_]*$/
      if (v_password.test(value)) {
        callback()
      } else {
        callback(new Error("以英文字母开头，只能包含英文字母、数字、下划线"))
      }
    }
    return {
      select: 1,
      registerForm: {},
      // 表单验证规则
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { validator: validatephone1, trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 2,
            max: 15,
            message: "长度在 2 到 15 个字符",
            trigger: "change"
          },
          { validator: validatePassWord, trigger: "change" }
        ],
        repassword: [
          { required: true, message: "请确认密码", trigger: "blur" },
          { validator: validaterepassword, trigger: "change" }
        ]
      }
    }
  },
  mounted() {
    // 监听回车事件
    document.onkeydown = e => {
      const _key = window.event.keyCode
      if (_key === 13) {
        this.submitForm("registerFormref")
      }
    }
    // 监听输入框内容，如果为空，则获取焦点
    if (this.registerForm.username === "") {
      this.$refs.username.focus()
    } else if (this.registerForm.password === "") {
      this.$refs.password.focus()
    }
  },
  methods: {
    // 提交表单
    submitForm(ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        delete this.registerForm.repassword
        console.log(this.registerForm)
        const { data: res } = await this.$http.post(this.select === 1 ? "user/doregist" : "user/doShopperRegist", this.registerForm)
        if (res.code === 200) {
          this.$message({
            type: "success",
            message: "注册成功！"
          })
          const user = res.obj
          window.sessionStorage.setItem("user", JSON.stringify(user))
          this.$router.push("/")
        } else {
          this.$message({
            type: "error",
            message: "注册失败！"
          })
        }
      })
    },
    reback() {
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
a:hover {
  text-decoration: none;
}
.login_container {
  height: 100%;
  background: url("http://47.115.58.32:8080/img/login-bg.jpg") 100%;
  background-color: rgb(46, 196, 183);
}
.bg {
  width: 390px;
  height: 530px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-50%, -50%);
}
.login-form {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  .el-button {
    width: 100%;
  }
  .el-form-item:last-child {
    text-align: right;
  }
}
.login_box {
  width: 350px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-50%, -50%);
  border-radius: 4px;
  h1 {
    width: 100%;
    margin: 20px 0;
    text-align: center;
    color: rgb(23, 160, 240);
  }
}
</style>
