<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{ "商品" }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="w">
      <!-- 商品列表 -->
      <div class="goods-list">
        <div class="list-item clearfix">
          <ul>
            <router-link :to="'/goods/details/' + item.tg_id" v-for="item in goodslist" :key="item.tg_id">
              <li>
                <img :src="item.tg_img.ttgi_url || '../assets/img/1.jpg'" width="180" height="180" />
                <h3 class="title">
                  {{ item.tg_title || "商品名" }}
                </h3>
                <p class="desc">
                  {{ item.tg_context || "商品简介" }}
                </p>
                <div class="prices">
                  <span>{{ "￥" + item.tg_price }}</span>
                </div>
              </li>
            </router-link>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      goodslist: []
    }
  },
  created() {
    this.getgoodslist()
  },
  methods: {
    async getgoodslist() {
      const { data: res } = await this.$http.get("goods/showAll")
      res.obj.some(item => {
        item.tg_img.ttgi_url = this.root + item.tg_img.ttgi_url
      })
      console.log(res)
      this.goodslist = res.obj
    }
  }
}
</script>
<style lang="less" scoped>
.goods-list {
  margin-top: 40px;
  .list-title {
    margin-bottom: 20px;
    span {
      margin-left: 30px;
      font-size: 24px;
    }
  }
  ul {
    padding: 0;
    list-style: none;
    li {
      float: left;
      padding: 20px 0;
      height: 400px;
      width: 286px;
      background-color: #fff;
      text-align: center;
      transition: all 0.5s;
      img {
        margin: 20px auto 25px;
      }
      .title {
        margin: 20px 10px 5px;
        font-size: 14px;
        font-weight: 400;
        color: #333;
      }
      .desc {
        margin: 10px 20px;
        height: 50px;
        font-size: 12px;
        color: #b0b0b0;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
      }
      .prices {
        color: #fa1d61;
      }
    }
    li:hover {
      transform: translateY(-10px);
      box-shadow: 1px 1px 15px 1px #00000030;
    }
    a:not(:nth-child(4n)) {
      > li {
        margin-right: 15px;
      }
    }
    a:not(:nth-child(-n + 4)) {
      > li {
        margin-top: 15px;
      }
    }
  }
}
</style>
